$icons-font-path: "../fonts/icons" !default;
$icons-version:   "c5a1d52cb40008f6d4ed65bf3f12d508b2fe8c88";

@font-face {
  font-family: 'icons';
  src: url('#{$icons-font-path}/icons.eot?#iefix&v=#{$icons-version}') format('embedded-opentype'),
       url('#{$icons-font-path}/icons.woff?v=#{$icons-version}') format('woff'),
       url('#{$icons-font-path}/icons.ttf?v=#{$icons-version}') format('truetype'),
       url('#{$icons-font-path}/icons.svg?v=#{$icons-version}#icons') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon,
.icon-lg,
.icon-large {
  font: normal normal normal 16px/1 icons;
  display: inline-block;
  text-decoration: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.icon-lg,
.icon-large { font-size: 32px; }

.icon-xl,
.icon-xlarge { font-size: 48px; }

.icon-xxl,
.icon-xxlarge,
.icon-massive { font-size: 64px; }

